<template>
  <el-container class="header">
      <router-link class="logoimg" :to="{ name:'nav1_1'}" ></router-link>
      <div class="nav">
        <router-link v-for="(item, index) in navarry" :to="{ name:item.name}" class="nav_noactive">{{item.con}}</router-link>
      </div>
    <div class="userbox"><span>欢迎您，</span>
      <el-dropdown class="usermenu"  trigger="click">
      <span :title="this.username" class="el-dropdown-link usercon">{{username}}<i class="el-icon-arrow-down el-icon--right"></i></span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>修改密码</el-dropdown-item>
          <el-dropdown-item>下载插件</el-dropdown-item>
          <el-dropdown-item>帮助</el-dropdown-item>
          <el-dropdown-item>注销</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-dialog title="系统配置" :visible.sync="dialogSetting">
      <el-form >
        <el-form-item label="主机地址">
          <el-input v-model="settingform.localhost"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogSetting = false">取 消</el-button>
        <el-button type="primary" @click="saveSetting()">保 存</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>
<script>
  export default {
    data () {
      return {
        navarry:[{name:'nav1_1',con:'云平台监控'},{name:'nav2_1',con:'仿真设计'},{name:'nav3_1',con:'高性能计算'},{name:'nav4_1',con:'仿真数据'},{name:'nav5_1',con:'统计分析'},{name:'nav6_1',con:'许可管理'},{name:'nav7_1',con:'系统设置'},{name:'nav8_1',con:'应用商店'}],
        username:"rvcpadmin111111111111",
        dialogSetting:false,
        settingform:{
          localhost:"http://localhost:8080/"
        },
      }
    },
    created:function () {
    },
    computed:{
    },
    methods:{
    }
  }
</script>

<style scoped>
  .header{
    height:60px;
    background-color: #313137;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 14px;
    font-weight: 900;
    color: #ffffff;
  }
  .nav{
    margin:0px 15px;
    min-width:880px;
    width:calc(100% - 280px - 260px);
    height:60px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .nav_noactive{
    height:60px;
    width:100px;
    min-width: 120px;
    line-height: 60px;
    background-color: #2d2f33;
    color: #ffffff;
  }
  .nav_noactive:hover{
    background-color: #242429;
  }
  .router-link-active {
    background-color: #2cc362!important;
  }
  .logoimg{
    background-color: #313137!important;
    background: url("../../../static/img/logo.png") no-repeat;
    background-size: contain;
    min-width:250px;
    width: 250px;
    height:50px;
    margin:5px 15px;
  }
  .userbox{
    height:50px;
    width:210px;
    min-width:210px;
    margin:5px 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    line-height: 50px;
  }
  .usercon{
    display: inline-block;
    max-width:120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    course:hand;
    cursor: pointer;
  }
  .usermenu{
    height:30px;
    color: #ffffff;
  }
  .usermenu span{
    height:30px;
    line-height: 30px;
  }
</style>



