/*前端路由配置*/
import Vue from 'vue'
import Router from 'vue-router'
/*引入用到的组件*/
import nav1_1 from '../components/nav1_1.vue'
import nav2_1 from '../components/nav2_1.vue'
Vue.use(Router)

var router = new Router({
  routes: [
    { name:'nav1_1', path: '/',component: nav1_1},
    { name:'nav2_1', path: 'nav2_1',component: nav2_1},
    { name:'nav3_1', path: 'nav3_1',component: nav2_1},
    { name:'nav4_1', path: 'nav4_1',component: nav2_1},
    { name:'nav5_1', path: 'nav5_1',component: nav2_1},
    { name:'nav6_1', path: 'nav6_1',component: nav2_1},
    { name:'nav7_1', path: 'nav7_1',component: nav2_1},
    { name:'nav8_1', path: 'nav8_1',component: nav2_1},

  ]
})
export default  router



